import PropTypes from 'prop-types';
import { useState } from 'react';
import { sentenceCase } from 'change-case';
// @mui
import {
  Stack,
  Button,
  TableRow,
  Checkbox,
  MenuItem,
  TableCell,
  IconButton,
  Link,
} from '@mui/material';
import { toast } from 'react-toastify';
import { Edit } from '@mui/icons-material';
// utils
import { fDate } from '../../../../../utils/formatTime';
import { fCurrency } from '../../../../../utils/formatNumber';
// components
import Label from '../../../../../components/label';
import Image from '../../../../../components/image';
import Iconify from '../../../../../components/iconify';
import MenuPopover from '../../../../../components/menu-popover';
import ConfirmDialog from '../../../../../components/confirm-dialog';
import { api } from '../../../../../Api/api';
import { fetchproduct } from '../../../../../redux/thunk/index';
// ----------------------------------------------------------------------
import { useDispatch, useSelector } from '../../../../../redux/store';
import { statusCode } from '../../../../../constants';

ProductTableRow.propTypes = {
  row: PropTypes.object,
  selected: PropTypes.bool,
  onEditRow: PropTypes.func,
  onViewRow: PropTypes.func,
  onSelectRow: PropTypes.func,
  onDeleteRow: PropTypes.func,
};

export default function ProductTableRow({
  row,
  selected,
  onSelectRow,
  onDeleteRow,
  onEditRow,
  onViewRow,
}) {
  const { id, name, img, created_at, inventoryType, price } = row;

  const [openConfirm, setOpenConfirm] = useState(false);

  const [openPopover, setOpenPopover] = useState(null);

  const disapatch = useDispatch();

  const handleOpenConfirm = () => {
    setOpenConfirm(true);
  };

  const handleCloseConfirm = () => {
    setOpenConfirm(false);
  };

  const handleOpenPopover = (event) => {
    setOpenPopover(event.currentTarget);
  };

  const handleClosePopover = () => {
    setOpenPopover(null);
  };

  return (
    <TableRow hover selected={selected}>
      <TableCell padding="checkbox">
        <Checkbox checked={selected} onClick={onSelectRow} />
      </TableCell>

      <TableCell>
        <Stack direction="row" alignItems="center" spacing={2}>
          <Image
            disabledEffect
            visibleByDefault
            alt={name}
            src={img}
            sx={{ borderRadius: 1.5, width: 48, height: 48 }}
          />

          <Link
            noWrap
            color="inherit"
            variant="subtitle2"
            onClick={onViewRow}
            sx={{ cursor: 'pointer' }}
          >
            {name}

          </Link>
        </Stack>
      </TableCell>

      <TableCell>{fDate(created_at)}</TableCell>

      {/* <TableCell align="center">
          <Label
            variant="soft"
            color={
              (inventoryType === 'out_of_stock' && 'error') ||
              (inventoryType === 'low_stock' && 'warning') ||
              'success'
            }
            sx={{ textTransform: 'capitalize' }}
          >
            {inventoryType ? sentenceCase(inventoryType) : ''}
          </Label>
        </TableCell> */}

      {/* <TableCell align="right">{fCurrency(price)}</TableCell> */}

      <TableCell align="right">
        <IconButton color='error' onClick={() => {
          const tosatid = toast.loading('Please wait...');
          api.productApi.Delete(id).then((res) => {
            if (res.data.code === statusCode.created) {
              disapatch(fetchproduct());
              //   setUpdate(false);
              toast.update(tosatid, { render: res.data.message, type: 'success', isLoading: false });
              setTimeout(() => {
                toast.dismiss(tosatid);
              }, 5000);
              return;
            }
            toast.update(tosatid, { render: res.data.message, type: 'error', isLoading: false });
            setTimeout(() => {
              toast.dismiss(tosatid);
            }, 5000);
          }).catch((res => {
            console.log(res);
            toast.update(tosatid, { render: 'some thing went wrong', type: 'error', isLoading: false });
            setTimeout(() => {
              toast.dismiss(tosatid);
            }, 5000);
          }))
        }} >
          <Iconify icon="eva:trash-2-outline" />
        </IconButton>
        <IconButton color='warning' onClick={() => onEditRow()} >
          {/* <Iconify icon="eva:trash-2-outline" /> */}
          <Edit />
        </IconButton>
      </TableCell>
    </TableRow>
  );
}
